import { Component, OnInit, HostBinding } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import {TranslateService} from 'ng2-translate';
import {UsefulDataService} from '../../services/useful-data.service';
import { CountriesDataService } from '../../services/countries-data.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
import { SharedModelService } from '../../services/shared-model.service';
import { ScrollService } from '../../services/scroll.service';
@Component({
  selector: 'app-professional-situation',
  templateUrl: './professional-situation.component.html',
  styleUrls: ['./professional-situation.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''},
})

export class ProfessionalSituationComponent implements OnInit {

    /**
   * @var {array} profissionalSituations
   * @var {string} selectedSituation
   */
  profissionalSituations = []
  employeeProfessionalCategories = [];
  eiProfessionalCategories = [];
  retirementProfessionalCategories = [];
  studentProfessionalCategories = [];
  contractTypes = [];
  activitySectors = [];
  selectedSituation;
  selectedCategory;
  selectedContrat;
  selectedActivitySector;
  countries = []
  selectedCity: string = 'FR';
  stepSubmitted: boolean = false;
  internalStep = 1;
  hideStep = false;
  holderTransitionState;
  @HostBinding('@routerTransition') routerAnimation;
    /**
   * @constructor
   * Constructor of ProfessionalSituationComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _location: Location,
              private _translate: TranslateService,
              private router: Router, 
              private route: ActivatedRoute,
              private _usefulDataService: UsefulDataService,
              private _headerSteps:HeaderStateService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _routerState: RouterStateService,
              private _countriesService: CountriesDataService,
              private _sharedModelService: SharedModelService,
              private _scroll: ScrollService
              ) { 
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
              }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this.route.params.subscribe(params => {
       if(params['internalStep'] && this._sharedModelService.model.accountType === 'jointAccount'){
         this.internalStep = parseInt(params['internalStep']);
       }
       if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 2 || (this.internalStep === 1 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
    });

    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.SITUATION,subStep:0});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
    this.updateFormValues()
    
    this._translate.onLangChange.subscribe((ev)=>{
      this.updateFormValues();
    })
  }

  /**
   * Load internal steps for joint account (one step per holder)
   * @param step: step to load
   */
  loadInternalStep(step){
    if(step != this.internalStep){
      if(step === 2){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['professional-situation/2'])
          }, 400);  
        }
      }
      else {
        this.goBack();
      }
    }
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back()
  }

  updateFormValues(){
    this._countriesService.getCountries(this._translate.currentLang).then((data) => {
      this.countries = data;
    })
    this.profissionalSituations = this._usefulDataService.getProfessionalSituation(this._translate.currentLang);
    this.employeeProfessionalCategories = this._usefulDataService.getEmployeeProfessionalCategories(this._translate.currentLang);
    this.eiProfessionalCategories = this._usefulDataService.getIndividualProfessionalCategories(this._translate.currentLang);
    this.retirementProfessionalCategories = this._usefulDataService.getRetirementProfessionalCategories(this._translate.currentLang);
    this.studentProfessionalCategories = this._usefulDataService.getStudentProfessionalCategories(this._translate.currentLang);
    this.contractTypes = this._usefulDataService.getContractTypes(this._translate.currentLang);
    this.activitySectors = this._usefulDataService.getSectorActivities(this._translate.currentLang);
  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.stepSubmitted = true;
    if(this._sharedModelService.model.accountType === 'jointAccount'){
      if(this.internalStep === 1 && this._sharedModelService.model.informations.situation.professionalSituation.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this._scroll.scrollTo(0, 500)
        this.holderTransitionState = 'leave';
        setTimeout(() => {
          this.hideStep = true;
          this.stepSubmitted = false;
          this.router.navigate(['professional-situation/2'])
        }, 400);
      }
      else if(this.internalStep === 2 && this._sharedModelService.modelHolder.informations.situation.professionalSituation.validate()){
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this._sharedModelService.mustSaveData.next(true);
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['monthly-income/1']);
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
      
    }
    else {
      if(this._sharedModelService.model.informations.situation.professionalSituation.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['monthly-income']);
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }

    }
    
  }

}



