import { Component, HostBinding, OnInit, ViewChild } from '@angular/core';
import {Location} from '@angular/common';
import {ActivatedRoute,Router} from '@angular/router';
import { TranslateService } from 'ng2-translate';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
import { CountriesDataService } from '../../services/countries-data.service';
import { ScrollService } from '../../services/scroll.service';
import { SharedModelService } from '../../services/shared-model.service';
import {Modal} from '../../components/modal.component';
import { environment } from '../../../environments/environment'

@Component({
  selector: 'app-fiscal-information',
  templateUrl: './fiscal-information.component.html',
  styleUrls: ['./fiscal-information.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''}
})
export class FiscalInformationComponent implements OnInit {
  
  /**
   * @var {array} numbersInLetters
   * @var {array} fiscalpaysAdd
   * @var {array} fiscalPays
   * @var {string} selectedPays
   */
  numbersInLetters=["hsbc-main.second","hsbc-main.third","hsbc-main.fourth"]
  fiscalpaysAdd = [];
  fiscalpaysAddHolder = [];
  fiscalPays = [];
  selectedPays = 'FR';
  stepSubmitted = false;
  msgWhereCountry = '';
  environment = environment;
  internalStep = 1;
  hideStep = false;
  holderTransitionState;
  @ViewChild('modalTaxWhere') modalTaxWhere: Modal;
  @HostBinding('@routerTransition') routerAnimation = "next";

  /**
   * @constructor
   * Constructor of FiscalInformationComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private _location: Location,private router: Router,
              private _translate: TranslateService, 
              private _headerSteps:HeaderStateService,
              private _sharedModelService: SharedModelService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _countriesDataService: CountriesDataService,
              private _routerState: RouterStateService,
              private route: ActivatedRoute,
              private _scroll: ScrollService) { 
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
    this.updateForm();
    _translate.onLangChange.subscribe((ev) => {
     this.updateForm();
    })
  }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this.route.params.subscribe(params => {
       if(params['internalStep'] && this._sharedModelService.model.accountType === 'jointAccount'){
         this.internalStep = parseInt(params['internalStep']);
       }
       if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 2 || (this.internalStep === 1 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
    });
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.SITUATION,subStep:2});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);

  }

  /**
   * Load internal steps for joint account (one step per holder)
   * @param step: step to load
   */
  loadInternalStep(step){
    if(step != this.internalStep){
      if(step === 2){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['fiscal-information/2'])
          }, 400);  
        }
      }
      else {
        this.goBack();
      }
    }
  }

  updateForm(){
    this._countriesDataService.getCountries(this._translate.currentLang).then((data) => {
      this.fiscalPays = data
    })
  }

  openModalTaxWhere(taxWhereNum){
    if(taxWhereNum === 1){
      this.msgWhereCountry = this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidence;
    }
    else if(taxWhereNum === 2){
      this.msgWhereCountry = this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidenceTwo;
    }
    else if(taxWhereNum === 3){
      this.msgWhereCountry = this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidenceThree;
    }

    this.modalTaxWhere._isOpened = true
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back();
  }

  /**
   * Add fiscal country
   * @function AddFiscalPays
   */
  AddFiscalPays(){
    this.fiscalpaysAdd.push({name:null});
    this._sharedModelService.model.informations.situation.taxInformation.numberOfSupFiscalCountries = this.fiscalpaysAdd.length;
  }

  AddFiscalPaysHolder(){
    this.fiscalpaysAddHolder.push({name:null});
    this._sharedModelService.modelHolder.informations.situation.taxInformation.numberOfSupFiscalCountries = this.fiscalpaysAdd.length;
  }

  /**
   * Remove last added fiscal country
   * @function removeLastPays
   */
  removeLastPays(){
    this.fiscalpaysAdd.pop();
    this._sharedModelService.model.informations.situation.taxInformation.numberOfSupFiscalCountries = this.fiscalpaysAdd.length;
  }

  /**
   * Remove last added fiscal country
   * @function removeLastPays
   */
  removeLastPaysHolder(){
    this.fiscalpaysAddHolder.pop();
    this._sharedModelService.modelHolder.informations.situation.taxInformation.numberOfSupFiscalCountries = this.fiscalpaysAdd.length;
  }

  /**
   * Move to the next step
   * @function next
   */
  next(){
    this.stepSubmitted = true;

    if(this._sharedModelService.model.accountType === 'jointAccount'){
      if(this.internalStep === 1 && this._sharedModelService.model.informations.situation.taxInformation.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this._scroll.scrollTo(0, 500)
        this.holderTransitionState = 'leave';
        setTimeout(() => {
          this.hideStep = true;
          this.stepSubmitted = false;
          this.router.navigate(['fiscal-information/2'])
        }, 400);
      }
      else if(this.internalStep === 2 && this._sharedModelService.modelHolder.informations.situation.taxInformation.validate()){
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this._sharedModelService.mustSaveData.next(true);
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['patrimony/1'])
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
    }
    else {

      if(this._sharedModelService.model.informations.situation.taxInformation.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this.stepSubmitted = false;
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['patrimony'])
        },50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }

    }
    
  }
  

}



