import { Component, HostBinding, OnInit } from '@angular/core';
import {ActivatedRoute,Router}  from '@angular/router';
import {Location} from '@angular/common';
import {TranslateService} from 'ng2-translate';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
import { ScrollService } from '../../services/scroll.service';
import { SharedModelService } from '../../services/shared-model.service';
import { CountriesDataService } from '../../services/countries-data.service';
import {UsefulDataService} from '../../services/useful-data.service';

@Component({
  selector: 'app-patrimony',
  templateUrl: './patrimony.component.html',
  styleUrls: ['./patrimony.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''}
})
export class PatrimonyComponent implements OnInit {

  /**
   * @var {array} familySituations
   * @var {string} selectedValue
   * @var {boolean} patrimonyChecked
   * @var {string} checkedItem1
   * @var {string} checkedItem2
   * @var {string} checkedItem3
   * @var {string} checkedItem4
   */
familySituations=[{label:'Madame',value:'md'},{label:'Monsieur',value:'mo'}]
amounts = [];
countries = [];
selectedValue : string = "";
patrimonyChecked : boolean = false;
checkedItem1 : string;
checkedItem2 : string;
checkedItem3 : string;
checkedItem4 : string;
stepSubmitted: boolean = false;
dirty: boolean = false;
internalStep = 1;
hideStep = false;
holderTransitionState;
@HostBinding('@routerTransition') routerAnimation = "next";

  /**
   * @constructor
   * Constructor of PatrimonyComponent class
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(translate: TranslateService,private router: Router,private _location:Location, 
              private _headerSteps:HeaderStateService,
              private _translate: TranslateService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _routerState: RouterStateService,
              private route: ActivatedRoute,
              private _scroll: ScrollService,
              private _countriesDataService: CountriesDataService,
              private _sharedModelService: SharedModelService,
              private _usefulDataService: UsefulDataService) { 
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
  }

  /**
   * Initialisations
   * @function ngOnInit
   */
  ngOnInit() {
    this.route.params.subscribe(params => {
       if(params['internalStep'] && this._sharedModelService.model.accountType === 'jointAccount'){
         this.internalStep = parseInt(params['internalStep']);
       }
       if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 2 || (this.internalStep === 1 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
       else if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 3 || (this.internalStep === 2 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
    });
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.SITUATION,subStep:3});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);

    this._translate.onLangChange.subscribe((ev)=>{
      this.updateFormValues();
    })
    this.updateFormValues();
  }

  /**
   * Load internal steps for joint account (one step per holder)
   * @param step: step to load
   */
  loadInternalStep(step){
    if(step != this.internalStep){
      if(step === 2){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['patrimony/2'])
          }, 400);  
        }
      }
      else if(step === 3){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['patrimony/3'])
          }, 400);  
        }
      }
      else {
        this.goBack();
      }
    }
  }

  updateFormValues(){
    this.amounts = this._usefulDataService.getPatrimonyAmounts(this._translate.currentLang);
    this._countriesDataService.getCountries(this._translate.currentLang).then((data) => {
      this.countries = data
    })
    if(this._sharedModelService.model.accountType === 'jointAccount'){
      this._sharedModelService.model.informations.situation.patrimony.mustValidateMutual = true;
    } 
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack(){
    this._location.back();
  }

  /**
   * Move to the next step
   * @function next
   */
  next(){
    this.stepSubmitted = true;

    if(this._sharedModelService.model.accountType === 'jointAccount'){
      if(this.internalStep === 1 && this._sharedModelService.model.informations.situation.patrimony.validate()){
        console.log('First step validated')
        this._sharedModelService.mustSaveData.next(true);
        this._scroll.scrollTo(0, 500)
        this.holderTransitionState = 'leave';
        setTimeout(() => {
          this.hideStep = true;
          this.stepSubmitted = false;
          this.router.navigate(['patrimony/2'])
        }, 400);
      }
      else if(this.internalStep === 2 && this._sharedModelService.modelHolder.informations.situation.patrimony.validate()){
        console.log('Second step validated')
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this._sharedModelService.mustSaveData.next(true);

        if(this._sharedModelService.model.informations.situation.patrimony.nopatrimoine !== true && this._sharedModelService.modelHolder.informations.situation.patrimony.nopatrimoine !== true){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['patrimony/3'])
          }, 400);
        }
        else {
          this.routerAnimation = 'prev';
          setTimeout(() => {
            this.router.navigate(['follow-account']);
          }, 50)
        }
      }
      else if(this.internalStep === 3 && this._sharedModelService.model.informations.situation.patrimony.validateMutual()){
        console.log('Third step validated')
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this._sharedModelService.mustSaveData.next(true);


        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['follow-account']);
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
    }
    else {
      if(this._sharedModelService.model.informations.situation.patrimony.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this.stepSubmitted = false;
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['follow-account']);
        },50)  
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
    }
    
  }

  /**
   * Check one item and remove the check from the other checked item
   * @function changeCheckedItem  
   */
  changeCheckedItem(checkedItem) {
    this.dirty = true;
    this._sharedModelService.model.informations.situation.patrimony.nopatrimoine = false;
    this._sharedModelService.model.informations.situation.patrimony.validatePatrimonyOrigin();
  }

  /**
   * Check one item and remove the check from the other checked item
   * @function changeCheckedItem  
   */
  changeCheckedItemHolder(checkedItem) {
    this.dirty = true;
    this._sharedModelService.modelHolder.informations.situation.patrimony.nopatrimoine = false;
    this._sharedModelService.modelHolder.informations.situation.patrimony.validatePatrimonyOrigin();
  }

}



