import {Injectable} from '@angular/core'
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router'
import {SharedModelService} from '../shared-model.service';

@Injectable()
export class CardUtilisationGuard implements CanActivate {
    constructor(private _sharedModelService: SharedModelService, private router: Router) {}
 
    public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

      try{
        let step = parseInt(route.params['internalStep']);
        if(this._sharedModelService.model.accountType === 'individualAccount' && this._sharedModelService.model.informations.account.creditCard.isCardTypeValidated){
           if(step){
             this.router.navigate(['card-utilisation']);
             return false
           }

          return true;
        }
        else if(this._sharedModelService.model.accountType === 'jointAccount'){
          if(step){
            this.router.navigate(['card-utilisation']);
            return false;
          }
          else {
            if(this._sharedModelService.model.informations.account.creditCard.isCardTypeValidated && this._sharedModelService.modelHolder.informations.account.creditCard.isCardTypeValidated){
              return true;  
            }
            else {

             this.router.navigate(['card-type/2']);
             return false;  
              
            }
          }
          
        }
        else {
          this.router.navigate(['card-type']);
          return false;
        }
      }
      catch (e){
        this.router.navigate(['card-type']);
        return false;
      }

     

    }
}


