import {Injectable} from '@angular/core'
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router'
import {SharedModelService} from '../shared-model.service';

@Injectable()
export class CardTypeGuard implements CanActivate {
    constructor(private _sharedModelService: SharedModelService, private router: Router) {}
 
    public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

      try{
        let step = parseInt(route.params['internalStep']);
        if(this._sharedModelService.model.accountType === 'individualAccount' && this._sharedModelService.model.informations.account.offer.isOfferValidated){
           if(step){
             this.router.navigate(['card-type']);
             return false
           }

          return true;
        }
        else if(this._sharedModelService.model.accountType === 'jointAccount'){
          if(!step){
            this.router.navigate(['card-type/1']);
            return false;
          }
          else if(step == 1){
            if(this._sharedModelService.model.informations.account.offer.isOfferValidated){
              return true;  
            }
            else {
              this.router.navigate(['your-account']);
              return false;
            }
          }
          else if(step == 2){
            if(this._sharedModelService.model.informations.account.creditCard.isCardTypeValidated && this._sharedModelService.modelHolder.informations.account.offer.isOfferValidated){
              return true;  
            }
            else {
              this.router.navigate(['card-type/1']);
              return false;
            }
          }
          
        }
        else {
          this.router.navigate(['your-account']);
          return false;
        }
      }
      catch (e){
        this.router.navigate(['your-account']);
        return false;
      }

     

    }
}


