import {Injectable} from '@angular/core'
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router'
import {SharedModelService} from '../shared-model.service';

@Injectable()
export class FiscalInformationGuard implements CanActivate {
    constructor(private _sharedModelService: SharedModelService, private router: Router) {}
 
    public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

      try{
        let step = parseInt(route.params['internalStep']);
        if(this._sharedModelService.model.accountType === 'individualAccount' && this._sharedModelService.model.informations.situation.isMonthlyIncomeValidate){
           if(step){
             this.router.navigate(['fiscal-information']);
             return false
           }

          return true;
        }
        else if(this._sharedModelService.model.accountType === 'jointAccount'){
          if(!step){
            this.router.navigate(['fiscal-information/1']);
            return false;
          }
          else if(step == 1){
            if(this._sharedModelService.model.informations.situation.isMonthlyIncomeValidate){
              return true;  
            }
            else {
              this.router.navigate(['monthly-income/2']);
              return false;
            }
          }
          else if(step == 2){
            if(this._sharedModelService.model.informations.situation.taxInformation.isTaxInformationValidate && this._sharedModelService.modelHolder.informations.situation.isMonthlyIncomeValidate){
              return true;  
            }
            else {
              this.router.navigate(['fiscal-information/1']);
              return false;
            }
          }
          
        }
        else {
          this.router.navigate(['monthly-income']);
          return false;
        }
      }
      catch (e){
        this.router.navigate(['monthly-income']);
        return false;
      }

     

    }
}


