import { Component, OnInit, HostBinding } from '@angular/core';
import { TranslateService } from 'ng2-translate';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import { SharedModelService } from '../../services/shared-model.service';
import { UsefulDataService } from '../../services/useful-data.service';
import { CountriesDataService } from '../../services/countries-data.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition } from '../../routing/router.animations';
import { ScrollService } from '../../services/scroll.service';

@Component({
  selector: 'app-contact-details',
  templateUrl: './contact-details.component.html',
  styleUrls: ['./contact-details.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''},
})
export class ContactDetailsComponent implements OnInit {

  /**
   * @var {Array} residenceStatus
   * @var {Date} dateVal
   * @var {Date} today
   */
  currentLocal = null;
  residenceStatus = [];
  openingHsbcAccountReasons = [];
  countries = [];
  dateVal: Date;
  today = new Date()
  liveAtAddressSinceMonth = ""
  liveAtAddressSinceYear = ""
  liveAtAddressSinceMonthHolder = ""
  liveAtAddressSinceYearHolder = ""
  stepSubmitted = false;
  @HostBinding('@routerTransition') routerAnimation;
  /**
   * @constructor
   * Constuctor of ContactDetailsComponent class
   * @param {TranslateService} translate translation service
   * @param {Router} router router service
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService service
   */
  constructor(
    private _translate: TranslateService,
    private router: Router, private _location: Location,
    private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService,
    private _countriesDataService: CountriesDataService,
    private _usefuldataService: UsefulDataService,
    private _routerState: RouterStateService,
    private _scroll: ScrollService) {
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
    _translate.onLangChange.subscribe((ev) => {
     this.updateForm();
    })
  }

  /**
   * Initialisations
   * @function ngOnInit
   */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.INFORMATION,subStep:3});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
     this.updateForm();

  }

  updateForm(){
     this._countriesDataService.getCountries(this._translate.currentLang).then((data) => {
      this.countries = data
    })
    this.currentLocal = this._countriesDataService.getLocale(this._translate.currentLang);
    this.residenceStatus = this._usefuldataService.getResidanceStatus(this._translate.currentLang)
    this.openingHsbcAccountReasons = this._usefuldataService.getOpeningHsbcAccountReason(this._translate.currentLang);
    if(this._sharedModelService.model.informations.contactDetail.livesAtThisAddressSince){
      this.onSelect(this._sharedModelService.model.informations.contactDetail.livesAtThisAddressSince);  
    }
    if(this._sharedModelService.model.accountType === 'jointAccount'){
      this._sharedModelService.modelHolder.informations.contactDetail.isHolder = true;
    }
  }

  towDigit(val: number){
    if(val < 10){
      return '0'+val
    }
    return val.toString()
  }

  /**
   * fired when a date is selected from the calendar
   * @param date 
   */
  onSelect(date){
     this.liveAtAddressSinceMonth = this.towDigit(date.getMonth() + 1)
     this.liveAtAddressSinceYear = date.getFullYear()
  }

  onSelectHolder(date){
     this.liveAtAddressSinceMonthHolder = this.towDigit(date.getMonth() + 1)
     this.liveAtAddressSinceYearHolder = date.getFullYear()
  }
  
 /**
  * check if a user have an other address 
  */
  userHasPreviousAdress(model){
   this._sharedModelService[model].informations.contactDetail.hasPreviousAddress = false;
    if(this._sharedModelService[model].informations.contactDetail.livesAtThisAddressSince){
      if(this._sharedModelService[model].informations.person.birthDate){
         
        let todayDate = new Date(),
            todayYear = todayDate.getFullYear(),
            todayMonth = todayDate.getMonth(),
            todayDay = todayDate.getDate(),
            livesAtThisAddressSinceDay = this._sharedModelService[model].informations.contactDetail.livesAtThisAddressSince.getDate(),
            livesAtThisAddressSinceMonth = this._sharedModelService[model].informations.contactDetail.livesAtThisAddressSince.getMonth(),
            livesAtThisAddressSinceYear = this._sharedModelService[model].informations.contactDetail.livesAtThisAddressSince.getFullYear();


        let timeDiff = Math.abs(todayDate.getTime() - this._sharedModelService[model].informations.contactDetail.livesAtThisAddressSince.getTime()),
            diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24)),
            diff = (diffDays / 365);
            
        this._sharedModelService[model].informations.contactDetail.hasPreviousAddress = diff <= 3
      }
    }
    return  this._sharedModelService[model].informations.contactDetail.hasPreviousAddress;
  }

  /**
   * fired when a textfield content changed, textfield is one of [birthDay,birthMonth,birthYear]
   * @param event 
   */
  onTfChange(event, model){
    let month, year;
    if(model === 'model'){
      month = this.liveAtAddressSinceMonth;
      year = this.liveAtAddressSinceYear;
    }
    else if(model === 'modelHolder'){
      month = this.liveAtAddressSinceMonthHolder;
      year = this.liveAtAddressSinceYearHolder;
    }

    try{
      if(month && month.trim().length == 2 && 
         year && year.trim().length == 4){
    
            let dateStr = month+'/30' +'/'+year
            let date = new Date(dateStr)
            if(this._sharedModelService[model].informations.contactDetail.validateDate(date)){
                  this._sharedModelService[model].informations.contactDetail.livesAtThisAddressSince = date;
                  this._sharedModelService[model].informations.contactDetail.validateLivesAtThisAddressSince();
            }
      }
      else {
        this._sharedModelService[model].informations.contactDetail.livesAtThisAddressSince = null;
        this._sharedModelService[model].informations.contactDetail.validateLivesAtThisAddressSince();
      }
      
    }catch(e){
      
    }
  }

  /**
 * Move to the previous step
 * @function goBack
 */
  goBack() {
    this._location.back();
  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.stepSubmitted = true;
    if(this._sharedModelService.model.accountType === 'jointAccount'){
      //this.stepSubmitted = false;
      let check = true;
      if(!this._sharedModelService.model.informations.contactDetail.validate()){
        check = false;
      }
      if(!this._sharedModelService.modelHolder.informations.contactDetail.validate()){
        check = false;
      }
      if(check){
        this._sharedModelService.mustSaveData.next(true);
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['professional-situation/1']);
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
    }
    else{
      if(this._sharedModelService.model.informations.contactDetail.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['professional-situation']);
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
    }
  }

}



