    getRecommendedTypeCard(){
      let sumMonthlyIncome = Number(this.monthlyIncome.monthlyNetSalary) + Number(this.monthlyIncome.BICBNCMonthly) + Number(this.monthlyIncome.pensionsMonthlyRetirement) + Number(this.monthlyIncome.allowancesMonthly) + Number(this.monthlyIncome.otherMonthlyIncome);

      // 'Visa Classic' 'Visa Premier' 'Gold Mastercard' 
       
       if(sumMonthlyIncome > 2500){
         return 'Visa Premier';
       }
       else if(this.patrimony.accountsPatrimony === '50-100' || this.patrimony.accountsPatrimony === '100-200' || this.patrimony.accountsPatrimony === '200'){
         return 'Visa Premier'
       }
       else if(this.patrimony.financialTitles === '50-100' || this.patrimony.financialTitles === '100-200' || this.patrimony.financialTitles === '200'){
         return 'Visa Premier'
       }
       else if(this.patrimony.assurancePatrimony === '50-100' || this.patrimony.assurancePatrimony === '100-200' || this.patrimony.assurancePatrimony === '200'){
         return 'Visa Premier'
       }
       else if(this.patrimony.otherActivePatrimony === '50-100' || this.patrimony.otherActivePatrimony === '100-200' || this.patrimony.otherActivePatrimony === '200'){
         return 'Visa Premier'
       }
       else if(this.patrimony.accountsPatrimony === '10-50' && this.patrimony.financialTitles === '10-50' && this.patrimony.assurancePatrimony === '10-50' && this.patrimony.otherActivePatrimony === '10-50'){
         return 'Visa Premier'
       }
       else {
         return 'Visa Classic'
       }

    }

