class TaxInformation {
    numberOfSupFiscalCountries = 0;

    countryOfTaxResidence: string = null;
    countryOfTaxResidenceError: string;
    taxIdentificationNumber: number;
    taxIdentificationNumberError: string;
    hasNoTaxIdentificationNumber: boolean;
    hasNoTaxIdentificationNumberError: string;
    noTaxIdentificationNumberOption: string;
    noTaxIdentificationNumberOptionError: string;
    reasonAbsenceNIF: string;
    reasonAbsenceNIFError: string;
    commentAbsenceNIF: string;
    commentAbsenceNIFError: string;

    countryOfTaxResidenceTwo: string = null;
    countryOfTaxResidenceTwoError: string;
    taxIdentificationNumberTwo: number;
    taxIdentificationNumberTwoError: string;
    hasNoTaxIdentificationNumberTwo: boolean;
    hasNoTaxIdentificationNumberTwoError: string;
    noTaxIdentificationNumberOptionTwo: string;
    noTaxIdentificationNumberOptionTwoError: string;
    reasonAbsenceNIFTwo: string;
    reasonAbsenceNIFTwoError: string;
    commentAbsenceNIFTwo: string;
    commentAbsenceNIFTwoError: string;

    countryOfTaxResidenceThree: string = null;
    countryOfTaxResidenceThreeError: string;
    taxIdentificationNumberThree: number;
    taxIdentificationNumberThreeError: string;
    hasNoTaxIdentificationNumberThree: boolean;
    hasNoTaxIdentificationNumberThreeError: string;
    noTaxIdentificationNumberOptionThree: string;
    noTaxIdentificationNumberOptionThreeError: string;
    reasonAbsenceNIFThree: string;
    reasonAbsenceNIFThreeError: string;
    commentAbsenceNIFThree: string;
    commentAbsenceNIFThreeError: string;

    isTaxInformationValidate: boolean = false;


    validateOnlyNumberAndDecimals(numbers){
      let re = /^[\d.]+$/;
      return re.test(numbers);
    }

    validateRequiredString(str: string){
        if(str && str.trim().length > 0){
            return true;
        }
        return false;
    }

    validateCountryOfTaxResidence(){
      if (!this.countryOfTaxResidence || this.countryOfTaxResidence.trim().length == 0) {
          this.countryOfTaxResidenceError = REQUIRED;
          return false;
      }
      else{
        this.countryOfTaxResidenceError = null;
        return true;
      }
    }


    validateTaxIdentificationNumber(){
      if(this.countryOfTaxResidence === 'FR'){
        this.taxIdentificationNumberError = null;
        return true;
      }
      if( (!this.taxIdentificationNumber || !this.validateRequiredString(this.taxIdentificationNumber.toString())) && !this.hasNoTaxIdentificationNumber){
        this.taxIdentificationNumberError = REQUIRED;
        return false;
      }
      // else if(!this.validateOnlyNumberAndDecimals(this.taxIdentificationNumber) && !this.hasNoTaxIdentificationNumber){
      //   this.taxIdentificationNumberError = INVALID_NUMBERS;
      //   return false;
      // }
      else if(!this.hasNoTaxIdentificationNumber){
        let regex;

        switch(this.countryOfTaxResidence){
          case 'GB':
            regex = /^([0-9a-zA-Z]{2})\s*([0-9]{2})\s*([0-9]{1})\s*([0-9]{1})\s*([0-9]{2})\s*([0-9a-zA-Z]{0,1})\s*([0-9]{0,1})\s*$/i;
            break;
          case 'CA':
            regex = /^([0-9]{3})\s*([0-9]{3})\s*([0-9]{3})\s*$/i;
            break;
          case 'BE':
            regex = /^([0-9]{6})\s*([0-9]{3})\s*([0-9]{2})\s*$/i;
            break;
          case 'CH':
            regex = /^(756)\s*\.*([0-9]{4})\s*\.*([0-9]{4})\s*\.*([0-9]{2})\s*$/i;
            break;
          case 'AT':
            regex = /^([0-9]{2})\s*\-*([0-9]{3})\s*\/*([0-9]{4})\s*$/i;
            break;
          case 'HK':
            regex = /^([a-zA-Z]{1,2})\s*([0-9]{6})\s*\(*([0-9|a|A]{1})\)*\s*$/i;
            break;
          case 'US':
            regex = /^(9)([0-9]{2})\s*([0-9]{2})\s*([0-9]{4})\s*$/i;
            break;
          case 'SG':
            regex = /^([s|t|f|g|S|T|F|G]{1})([0-9]{7})([0-9a-zA-Z]{1})\s*$/i;
            break;
        }

        if(regex && !regex.test(this.taxIdentificationNumber)){
          this.taxIdentificationNumberError = INVALID_FORMAT;
          return false;
        }
        else {
          this.taxIdentificationNumberError = null;
          return true; 
        }
      }
      else if(this.hasNoTaxIdentificationNumber){
        this.taxIdentificationNumberError = null;
        return false;
      }
      else {
        this.taxIdentificationNumberError = null;
        return true;
      }
    }

    validateNoTaxIdentificationNumberOption(){
      if(!this.noTaxIdentificationNumberOption || !this.validateRequiredString(this.noTaxIdentificationNumberOption.toString())){
         this.noTaxIdentificationNumberOptionError = REQUIRED;
         return false;
      }
      else if(this.noTaxIdentificationNumberOption === 'Motif B' && (!this.reasonAbsenceNIF || !this.validateRequiredString(this.reasonAbsenceNIF) )){
        this.noTaxIdentificationNumberOptionError = null;
        this.reasonAbsenceNIFError = REQUIRED;
        return false;
      }
      this.noTaxIdentificationNumberOptionError = null;
      this.reasonAbsenceNIFError = null;
      return true;
    }

    validateTaxIdentificationNumberTwo(){
      if( (!this.taxIdentificationNumberTwo || !this.validateRequiredString(this.taxIdentificationNumberTwo.toString())) && !this.hasNoTaxIdentificationNumberTwo){
        this.taxIdentificationNumberTwoError = REQUIRED;
        return false;
      }
      else if(!this.validateOnlyNumberAndDecimals(this.taxIdentificationNumberTwo) && !this.hasNoTaxIdentificationNumberTwo){
        this.taxIdentificationNumberTwoError = INVALID_NUMBERS;
        return false;
      }
      else if(this.hasNoTaxIdentificationNumberTwo){
        this.taxIdentificationNumberTwoError = null;
        return false;
      }
      else {
        this.taxIdentificationNumberTwoError = null;
        return true;
      }
    }

    validateCountryOfTaxResidenceTwo(){
      if (!this.countryOfTaxResidenceTwo || this.countryOfTaxResidenceTwo.trim().length == 0) {
          this.countryOfTaxResidenceTwoError = REQUIRED;
          return false;
      }
      else if(this.countryOfTaxResidenceTwo === this.countryOfTaxResidence){
        this.countryOfTaxResidenceTwoError = INVALID_COUNTRY;
        return false;
      }
      else{
        this.countryOfTaxResidenceTwoError = null;
        return true;
      }
    }

    validateNoTaxIdentificationNumberOptionTwo(){
      if(!this.noTaxIdentificationNumberOptionTwo || !this.validateRequiredString(this.noTaxIdentificationNumberOptionTwo.toString())){
         this.noTaxIdentificationNumberOptionTwoError = REQUIRED;
         return false;
      }
      else if(this.noTaxIdentificationNumberOptionTwo === 'Motif B' && (!this.reasonAbsenceNIFTwo || !this.validateRequiredString(this.reasonAbsenceNIFTwo) )){
        this.noTaxIdentificationNumberOptionTwoError = null;
        this.reasonAbsenceNIFTwoError = REQUIRED;
        return false;
      }
      
      this.noTaxIdentificationNumberOptionTwoError = null;
      this.reasonAbsenceNIFTwoError = null;
      return true;
    }

    validateCountryOfTaxResidenceThree(){
      if (!this.countryOfTaxResidenceThree || this.countryOfTaxResidenceThree.trim().length == 0) {
          this.countryOfTaxResidenceThreeError = REQUIRED;
          return false;
      }
      else if(this.countryOfTaxResidenceThree === this.countryOfTaxResidence || this.countryOfTaxResidenceThree === this.countryOfTaxResidenceTwo){
        this.countryOfTaxResidenceThreeError = INVALID_COUNTRY;
        return false;
      }
      else{
        this.countryOfTaxResidenceThreeError = null;
        return true;
      }
    }

    validateTaxIdentificationNumberThree(){
      if( (!this.taxIdentificationNumberThree || !this.validateRequiredString(this.taxIdentificationNumberThree.toString())) && !this.hasNoTaxIdentificationNumberThree){
        this.taxIdentificationNumberThreeError = REQUIRED;
        return false;
      }
      else if(!this.validateOnlyNumberAndDecimals(this.taxIdentificationNumberThree) && !this.hasNoTaxIdentificationNumberThree){
        this.taxIdentificationNumberThreeError = INVALID_NUMBERS;
        return false;
      }
      else if(this.hasNoTaxIdentificationNumberThree){
        this.taxIdentificationNumberThreeError = null;
        return false;
      }
      else {
        this.taxIdentificationNumberThreeError = null;
        return true;
      }
    }

    validateNoTaxIdentificationNumberOptionThree(){
      if(!this.noTaxIdentificationNumberOptionThree || !this.validateRequiredString(this.noTaxIdentificationNumberOptionThree.toString())){
         this.noTaxIdentificationNumberOptionThreeError = REQUIRED;
         return false;
      }
      else if(this.noTaxIdentificationNumberOptionThree === 'Motif B' && (!this.reasonAbsenceNIFThree || !this.validateRequiredString(this.reasonAbsenceNIFThree) )){
        this.noTaxIdentificationNumberOptionThreeError = null;
        this.reasonAbsenceNIFThreeError = REQUIRED;
        return false;
      }
      
      this.noTaxIdentificationNumberOptionThreeError = null;
      this.reasonAbsenceNIFThreeError = null;
      return true;
    }



    validate(){
      let hasErrors = false;

      if(!this.validateCountryOfTaxResidence()){
        hasErrors = true;
      }

      if(!this.validateTaxIdentificationNumber()){
        if(!this.validateNoTaxIdentificationNumberOption()){
          hasErrors = true;
        }
      }

      

      if(this.numberOfSupFiscalCountries >= 1){

        if(!this.validateCountryOfTaxResidenceTwo()){
          hasErrors = true;
        }

        if(!this.validateTaxIdentificationNumberTwo()){


          if(!this.validateNoTaxIdentificationNumberOptionTwo()){
            hasErrors = true;
          }
        }

        
      }

      if(this.numberOfSupFiscalCountries >= 2){

        if(!this.validateCountryOfTaxResidenceThree()){
          hasErrors = true;
        }

        if(!this.validateTaxIdentificationNumberThree()){
          if(!this.validateNoTaxIdentificationNumberOptionThree()){
            hasErrors = true;
          }    
        }

            
      }

      if(!hasErrors){
        this.isTaxInformationValidate = true;
      }

      return !hasErrors;
    }


}

