import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { TranslateService } from 'ng2-translate';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
import { environment } from '../../environments/environment'

@Injectable()
export class CountriesDataService {
  private __data: any[] = [];
  private __data_loaded = false
  private __data_lang;
  localeFR = {
    firstDayOfWeek: 0,
    dayNames: ["Dimanche", "Lundi", "Mardi", "Mercredi", "Jeudi", "Vendredi", "Samedi"],
    dayNamesShort: ["Dim", "Lun", "Mar", "Mer", "Jeu", "Ven", "Sam"],
    dayNamesMin: ["Di", "Lu", "Ma", "Me", "Je", "Ve", "Sa"],
    monthNames: ["Janvier", "Février", "Mars", "Avril", "Mai", "Juin", "Juillet", "Août", "Septembre", "Octobre", "Novembre", "Décembre"],
    monthNamesShort: ["janv.", "févr.", "mars", "avril", "mai", "juin", "juil.", "août", "sept.", "oct.", "nov.", "déc."]
  }
  constructor(private http: Http, private translate: TranslateService) {
    this.__data_lang = this.translate.currentLang;
    this.translate.onLangChange.subscribe((ev)=>{
      this.__data_lang = ev.lang;
    })
  }
  getData(lang) {
    if (this.__data_loaded) {
      return new Promise<any[]>((resolve, reject) => {
        this.resolvePromise(resolve)
      })
    } else {
      if (lang == 'fr') {
        return new Promise<any[]>((resolve, reject) => {
          this.http.get(environment.assetsPath + "assets/data/countries_fr.json")
            .subscribe((data) => {
              this.__data = data.json()
              this.__data_loaded = true
              this.resolvePromise(resolve)
            });
        })
      }
      else {
       return new Promise<any[]>((resolve, reject) => {
          this.http.get(environment.assetsPath + "assets/data/countries.json")
            .subscribe((data) => {
              this.__data = data.json()
              this.__data_loaded = true
              this.resolvePromise(resolve)
            });
        }) 
      }
    }
  }
  getCountryLabel(value, callback){
    this.getCountries(this.__data_lang).then((countries) => {
      let option = countries.filter((val) => {
        return val.value === value;
      })

      if(!option[0] || !option[0].label){
        callback('------')
      }
      else {
        callback(option[0].label); 
      }
    })
  }
  getCountries(lang) {
    if (this.__data_loaded && lang == this.__data_lang) {
      return new Promise<any[]>((resolve, reject) => {
        this.getCountriesData(resolve, lang)
      })
    } else {
      if (lang == 'fr') {
        return new Promise<any[]>((resolve, reject) => {
          this.http.get(environment.assetsPath + "assets/data/countries_fr.json")
            .subscribe((data) => {
              this.__data = data.json()
              this.__data_loaded = true
              this.__data_lang = lang;
              this.getCountriesData(resolve, lang)
            });
        })
      }
      else {
       return new Promise<any[]>((resolve, reject) => {
          this.http.get(environment.assetsPath + "assets/data/countries.json")
            .subscribe((data) => {
              this.__data = data.json()
              this.__data_loaded = true;
              this.__data_lang = lang;
              this.getCountriesData(resolve, lang)
            });
        }) 
      }
    }
  }
  getCountriesData(resolve: Function, lang) {
    let data = [];
    let priv = ['FR', 'CH', 'DE', 'LU', 'US', 'IT', 'ES', 'UK']
    let toSort = ['FR', 'AT', 'BE', 'CA', 'AE', 'US', 'HK', 'GB', 'SG', 'CH'];
    let customItems = [];
    for (let i = 0; i < this.__data.length; i++) {
        if(toSort.indexOf(this.__data[i].code) == -1){
           data.push({
            label: this.__data[i].name, value: this.__data[i].code
          })
        }
    }

    data.unshift({label: '--------------', value: ''})
    for(let g = toSort.length - 1; g >= 0; g--){

      let itemIndex = this.arrayObjectIndexOf(this.__data, toSort[g], 'code');
      let item = this.__data[itemIndex];

      g === 0 ? data.unshift({label: '--------------', value: ''}) : null;

      data.unshift({
        label: item.name, value: item.code
      })
    }

    
    if(lang == 'fr'){
      data.unshift({
        label: 'Veuillez sélectionner une valeur', value: ''
      })
    }
    else {
      data.unshift({
        label: 'Please choose a value', value: ''
      })
    }
    

    resolve(data);
  }
  resolvePromise(resolve: Function) {
    let data = [];
    let priv = ['FR', 'CH', 'DE', 'LU', 'US', 'IT', 'ES', 'UK']
    for (let i = 0; i < this.__data.length; i++) {
      //if (priv.indexOf(this.__data[i].code) > -1) {
        data.push({
          label: this.__data[i].code, value: this.__data[i].dial_code
        })


      //}
    }
    data.sort(function(a,b) {return (a.label > b.label) ? 1 : ((b.label > a.label) ? -1 : 0);} );
    resolve(data);
  }
  getLocale(lang) {
    if (lang == 'fr') {
      return this.localeFR
    } else {
      return null
    }
  }
  arrayObjectIndexOf(myArray, searchTerm, property) {
      for(var i = 0, len = myArray.length; i < len; i++) {
          if (myArray[i][property] === searchTerm) return i;
      }
      return -1;
  }
}



