import {Injectable} from '@angular/core'
import {ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot} from '@angular/router'
import {SharedModelService} from '../shared-model.service';

@Injectable()
export class YourAccountGuard implements CanActivate {
    constructor(private _sharedModelService: SharedModelService, private router: Router) {}
 
    public canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot) {

      try{
        let step = parseInt(route.params['internalStep']);
        if(this._sharedModelService.model.accountType === 'individualAccount' && this._sharedModelService.model.informations.account.isAgencyTypeValidated){
           if(step){
             this.router.navigate(['your-account']);
             return false
           }
          return true;
        }
        else if(this._sharedModelService.model.accountType === 'jointAccount'){
          if(step){
            this.router.navigate(['your-account']);
            return false;
          }
          else {
            if(this._sharedModelService.model.informations.account.isAgencyTypeValidated){
              return true;  
            }
            else {

              this.router.navigate(['follow-account']);
              return false;
              
              
            }
          }
          
        }
        else {
          this.router.navigate(['follow-account']);
          return false;
        }
      }
      catch (e){
        this.router.navigate(['follow-account']);
        return false;
      }

    }
}


