import { Component, OnInit, HostBinding } from '@angular/core';
import { TranslateService } from 'ng2-translate';
import { ActivatedRoute, Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import {SharedModelService} from '../../services/shared-model.service';
import { ScrollService } from '../../services/scroll.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
import { environment } from '../../../environments/environment'

@Component({
  selector: 'app-credit-card',
  templateUrl: './credit-card.component.html',
  styleUrls: ['./credit-card.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''},
})
export class CreditCardComponent implements OnInit {

/**
   * @var {string} cardType
   * @var {string} showAdvantage
   * @var {string} debitCreditCard
   */
  cardType: string;
  recommendedCardType: string;
  showAdvantage: string;
  debitCreditCard: string = 'dd';
  environment = environment;
  stepSubmitted = false;
  dirty = false;
  internalStep = 1;
  hideStep = false;
  holderTransitionState;
  @HostBinding('@routerTransition') routerAnimation;

  /**
   * @constructor 
   * Constuctor of CreditCardComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService
   */
  constructor(private router: Router, private _location: Location,
    private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService,
    private _routerState: RouterStateService,
    private route: ActivatedRoute,
    private _scroll: ScrollService
    ) {
      this.routerAnimation = this._routerState.lastAction;
      setTimeout(() => {
        this.routerAnimation = 'next'
      }, 500);

      this._sharedModelService.model.informations.account.creditCard.recommendedTypeCard = this._sharedModelService.model.informations.situation.getRecommendedTypeCard();
      if(!this._sharedModelService.model.informations.account.creditCard.typeCard){
        this._sharedModelService.model.informations.account.creditCard.typeCard = this._sharedModelService.model.informations.situation.getRecommendedTypeCard();  
      }
    }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this.route.params.subscribe(params => {
       if(params['internalStep'] && this._sharedModelService.model.accountType === 'jointAccount'){
         this.internalStep = parseInt(params['internalStep']);
       }
       if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 2 || (this.internalStep === 1 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
    });
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.ACCOUNT,subStep:2});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

  updateForm(){

  }

  /**
   * Load internal steps for joint account (one step per holder)
   * @param step: step to load
   */
  loadInternalStep(step){
    if(step != this.internalStep){
      if(step === 2){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['card-type/2'])
          }, 400);  
        }
      }
      else {
        this.goBack();
      }
    }
  }

  /**
 * Move to the previous step
 * @function goBack
 */
  goBack() {
    this._location.back()
  }

/**
 * Move to the next step
 * @function next
 * @param {string} type The type of the credit card
 */
  next(type) {
    this.stepSubmitted = true;
    if(this._sharedModelService.model.accountType === 'jointAccount'){
      if(this.internalStep === 1 && this._sharedModelService.model.informations.account.creditCard.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this._scroll.scrollTo(0, 500)
        this.holderTransitionState = 'leave';
        setTimeout(() => {
          this.hideStep = true;
          this.stepSubmitted = false;
          this.router.navigate(['card-type/2'])
        }, 400);
      }
      else if(this.internalStep === 2 && this._sharedModelService.modelHolder.informations.account.creditCard.validate()){
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this._sharedModelService.mustSaveData.next(true);
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['card-utilisation']);
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
    }
    else {
      this._sharedModelService.model.informations.account.creditCard.typeCard = type;
      //this._sharedModelService.model.informations.account.creditCard.debitType = this.debitCreditCard;
      
      if(this._sharedModelService.model.informations.account.creditCard.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this.stepSubmitted = false;
        this.routerAnimation = 'prev';

        setTimeout(() => {
          this.router.navigate(['card-utilisation']);
        }, 50);  
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }

    }
    
  }

  closeAdvantages() {
    this.showAdvantage = "";
  }

/**
 * Move to the next step
 * @function next
 * @param {FillDataStepsChangerService} event The event service
 * @param {string} type The type of the credit card
 */
  showAdvantageOf(event,type){
    event.stopPropagation();
    this.showAdvantage = type;
    this._sharedModelService.model.informations.account.creditCard.typeCard = type;
  }

  showAdvantageOfHolder(event,type){
    event.stopPropagation();
    this.showAdvantage = type;
    this._sharedModelService.modelHolder.informations.account.creditCard.typeCard = type;
  }

  reinitCard(){
    if(this._sharedModelService.model.informations.account.creditCard.noIntendCard){
      this._sharedModelService.model.informations.account.creditCard.typeCard = null
      this.showAdvantage = null;
    }
  }

  reinitCardHolder(){
    if(this._sharedModelService.modelHolder.informations.account.creditCard.noIntendCard){
      this._sharedModelService.modelHolder.informations.account.creditCard.typeCard = null
      this.showAdvantage = null;
    }
  }

  creditCardType(type) {
    this._sharedModelService.model.informations.account.creditCard.typeCard = type;
    this.showAdvantage = null;
  }

  creditCardTypeHolder(type) {
    this._sharedModelService.modelHolder.informations.account.creditCard.typeCard = type;
    this.showAdvantage = null;
  }

}



