import { Component, HostBinding, OnInit } from '@angular/core';
import {ActivatedRoute,Router}  from '@angular/router';
import {Location} from '@angular/common';
import {TranslateService} from 'ng2-translate';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import { ScrollService } from '../../services/scroll.service';
import { SharedModelService } from '../../services/shared-model.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition, holderTransition } from '../../routing/router.animations';
@Component({
  selector: 'app-monthly-income',
  templateUrl: './monthly-income.component.html',
  styleUrls: ['./monthly-income.component.css'],
  animations: [routerTransition(), holderTransition()],
  host: {'[@routerTransition]': ''},
})
export class MonthlyIncomeComponent implements OnInit {

/**
 * @var {boolean} anotherSourceOfIncomeAdded
 */
anotherSourceOfIncomeAdded : boolean = false;
monthlyNetSalary : string;
BICBNCMonthly : number;
pensionsMonthlyRetirement : number;
allowancesMonthly : number;
otherMonthlyIncome : number;
stepSubmitted: boolean = false;
internalStep = 1;
hideStep = false;
holderTransitionState;
@HostBinding('@routerTransition') routerAnimation;


  /**
   * @constructor
   * Constructor of MonthlyIncomeComponent class
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(translate: TranslateService,private router: Router,private _location:Location, 
              private _headerSteps:HeaderStateService,
              private _fillDataStepService: FillDataStepsChangerService,
              private route: ActivatedRoute,
              private _routerState: RouterStateService,
              private _scroll: ScrollService,
              private _sharedModelService: SharedModelService) { 
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
  }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this.route.params.subscribe(params => {
       if(params['internalStep'] && this._sharedModelService.model.accountType === 'jointAccount'){
         this.internalStep = parseInt(params['internalStep']);
       }
       if(this._sharedModelService.model.accountType === 'jointAccount' && ( this.internalStep === 2 || (this.internalStep === 1 && this._routerState.lastAction == 'prev'))){
         this.holderTransitionState = null;  
         setTimeout(() => {
           this.hideStep = false;
           this.holderTransitionState = 'enter'  
         }, 50);  
       }
    });
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.SITUATION,subStep:1});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

  /**
   * Load internal steps for joint account (one step per holder)
   * @param step: step to load
   */
  loadInternalStep(step){
    if(step != this.internalStep){
      if(step === 2){
        this.stepSubmitted = true;
        if(this._sharedModelService.model.informations.person.validateInformationsSecondPage()){
          this._scroll.scrollTo(0, 500)
          this.holderTransitionState = 'leave';
          setTimeout(() => {
            this.hideStep = true;
            this.stepSubmitted = false;
            this.router.navigate(['monthly-income/2'])
          }, 400);  
        }
      }
      else {
        this.goBack();
      }
    }
  }

  onMonthlyNetSalaryChange(event) {
    if(event.value === undefined)
      console.log("undefined")
    else if(event.value.slice(-1) != '$') {
      this.monthlyNetSalary = event.value;
      this.monthlyNetSalary = this.monthlyNetSalary.replace(/\D/g,'');
      this._sharedModelService.model.informations.situation.monthlyIncome.monthlyNetSalary = parseFloat(this.monthlyNetSalary);
      if(!parseFloat(this.monthlyNetSalary) && parseFloat(this.monthlyNetSalary) !== 0){
        this._sharedModelService.model.informations.situation.monthlyIncome.monthlyNetSalaryError = "hsbc-main.error_field_only_numbers";
      }
      else {
        this._sharedModelService.model.informations.situation.validateMonthlyNetSalary(null, false);  
      }
      
      this.monthlyNetSalary = this.monthlyNetSalary.concat("$")
      
    }
  }

  onMonthlyNetSalaryChangeHolder(event) {
    if(event.value === undefined)
      console.log("undefined")
    else if(event.value.slice(-1) != '$') {
      this.monthlyNetSalary = event.value;
      this.monthlyNetSalary = this.monthlyNetSalary.replace(/\D/g,'');
      this._sharedModelService.modelHolder.informations.situation.monthlyIncome.monthlyNetSalary = parseFloat(this.monthlyNetSalary);
      if(!parseFloat(this.monthlyNetSalary) && parseFloat(this.monthlyNetSalary) !== 0){
        this._sharedModelService.modelHolder.informations.situation.monthlyIncome.monthlyNetSalaryError = "hsbc-main.error_field_only_numbers";
      }
      else {
        this._sharedModelService.modelHolder.informations.situation.validateMonthlyNetSalary(null, false);  
      }
      
      this.monthlyNetSalary = this.monthlyNetSalary.concat("$")
      
    }
  }


  /**
   * Add another source of income
   * @function addAnotherSourceOfIncome
   */
  addAnotherSourceOfIncome() {
    this.anotherSourceOfIncomeAdded = true;
    this._sharedModelService.model.informations.situation.anotherSourceOfIncomeAdded = true;
  }

  /**
   * Cancel the other source of income
   * @function Cancel
   */
  Cancel() {
    this.anotherSourceOfIncomeAdded = false;
    this._sharedModelService.model.informations.situation.anotherSourceOfIncomeAdded = false;
  }

  /**
   * Add another source of income
   * @function addAnotherSourceOfIncomeHolder
   */
  addAnotherSourceOfIncomeHolder() {
    this._sharedModelService.modelHolder.informations.situation.anotherSourceOfIncomeAdded = true;
  }

  /**
   * CancelHolder the other source of income
   * @function CancelHolder
   */
  CancelHolder() {
    this._sharedModelService.modelHolder.informations.situation.anotherSourceOfIncomeAdded = false;
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack(){
    this._location.back();
  }

  /**
   * Move to the next step
   * @function next
   */
  next(){
    this.stepSubmitted = true;
    if(this._sharedModelService.model.accountType === 'jointAccount'){
      if(this.internalStep === 1 && this._sharedModelService.model.informations.situation.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this._scroll.scrollTo(0, 500)
        this.holderTransitionState = 'leave';
        setTimeout(() => {
          this.hideStep = true;
          this.stepSubmitted = false;
          this.router.navigate(['monthly-income/2'])
        }, 400);
      }
      else if(this.internalStep === 2 && this._sharedModelService.modelHolder.informations.situation.validate()){
        this.stepSubmitted = false;
        //this._sharedModelService.mustSaveData.next(true);
        this._sharedModelService.mustSaveData.next(true);
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['fiscal-information/1']);
        }, 50)
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }
    }
    else {
      if(this._sharedModelService.model.informations.situation.validate()){
        this._sharedModelService.mustSaveData.next(true);
        this.stepSubmitted = false;
        this.routerAnimation = 'prev';
        setTimeout(() => {
          this.router.navigate(['fiscal-information']);
        }, 50);  
      }
      else {
        setTimeout(() => {
          this._scroll.scrollToFirstBadField()    
        })
      }

    }
    
    
  }

}



