import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import {StepIndex} from '../components/steps.component';
@Injectable()
export class FillDataStepsChangerService {
  private _steps : BehaviorSubject<StepIndex>;
  private __currentStep = {step:FILL_DATA_STEPS.INFORMATION,subStep:0}
  constructor() { 
     this._steps = new BehaviorSubject(this.__currentStep);
  }
  getCurrentStep(){
    return this._steps.asObservable();
  }
  setCurrentStep(step:StepIndex){
    this.__currentStep = step;
    this._steps.next(step);
  }
}
export enum FILL_DATA_STEPS{
  INFORMATION = 0,
  SITUATION,
  ACCOUNT,
  VALIDATION
}



